nav {
  display: flex;
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  padding: 1% 0 1% 0;
  background-color: rgb(61, 40, 0);
  color: gold;
}

.left-indent {
  float: left;
  margin-left: 5%;
}

.centered {
  margin: auto;
}

.right-indent {
  float: right;
  margin-right: 5%;
}

body {
  padding: 0;
  margin: 0 0;
  font-family: sans-serif;
}

.jumbotron {
  background-color: gold;
  color: white;
}

.jumbotron h1 {
  font-weight: bold;
}

.jumbotron p {
  font-size: 24px;
  font-weight: bold;
}

.card {
  width: 200px;
  height: 150px;
  margin: 15px 45px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  border: 3px solid white;
  float: left;
}

img {
  width: 120px;
  height: 130px;
  float: left;
  margin: 0;
}

footer {
background-color: rgb(61, 40, 0);
  width: 100%;
  height: 50px;
  font-size: 12px;
  padding: 10px;
}

.leftSide {
  float: left;
}

.rightSide {
  float: right;
  margin-right: 3%;
}

h5 {
  color: gold;
  font-size: 14px;
  margin: 8px auto 0 40px;
}

a {
  color: gold;
  padding: 10px;
}

a:hover {
  color: white;
}
.card-area {
  height: 528px;
  max-width: 90%;
  margin: 10% 10% 5%;
  background: none;
}

body {
  background-image: url(/react-memory-game/static/media/cuccoEvolBackground.4306ad10.jpg);
  background-size: 97%
}


